$themes: (
  black: (
    page-bgc: #e5e5e5,
    container-horizontal-padding: 41px,
    header-bgc: #2c2c2c,
    header-border-radius: 0 0 30px 30px,
    header-hight-mobile: 68px,
    header-hight-desktop: 120px,
    header-margin-bottom: 30px,
    footer-border-radius: 30px 30px 0 0,
    slider-border-radius: 30px,
    slider-border-radius-desktop: 30px 0 30px 0,
    experiance-card-image-border-radius: 16px,
    section-bgc: #fff,
    section-border-radius: 30px,
    about-bg-image: url(../images/Shapes.png),
    testimonial-color1: #2060f6,
    testimonial-color2: #2060f6,
    testimonial-color3: #2060f6,
    testimonial-color4: #2060f6,
    button-bgc: #2060f6,
    button-hover-bgc: #144ed4,
    button-active-bgc: #144ac9,
    button-border-radius: 8px,
    text-on-dark: #fff,
    text-color: #253757,
    text-subcolor: #6c788b,
    text-color-superlight: #c0cdd7,
    transition-time: 0.3s,
  ),
  blue: (
    page-bgc: #e5e5e5,
    container-horizontal-padding: 41px,
    header-bgc: #008aff,
    header-border-radius: 0 0 0 200px,
    header-hight-mobile: 68px,
    header-hight-desktop: 120px,
    header-margin-bottom: 30px,
    footer-border-radius: 0
    clamp(30px, calc(1.88rem + ((1vw - 3.2px) * 12.78)), 120px) 0 0,
    /* 30px @ 320px increasing to 120px @ 1024px */ slider-border-radius:
    clamp(30px, calc(1.88rem + ((1vw - 3.2px) * 12.78)), 120px)
    clamp(30px, calc(1.88rem + ((1vw - 3.2px) * 12.78)), 120px) 0
    clamp(30px, calc(1.88rem + ((1vw - 3.2px) * 12.78)), 120px),
    slider-border-radius-desktop: 120px 0 0 0,
    experiance-card-image-border-radius: 16px,
    section-bgc: #fff,
    section-border-radius: 0 0 0 0,
    about-bg-image: url(../images/Shapes-light.png),
    testimonial-color1: #56ccf2,
    testimonial-color2: #fcc304,
    testimonial-color3: #f25656,
    testimonial-color4: #15d850,
    button-bgc: #ff9c8e,
    button-hover-bgc: #ff7e6c,
    button-active-bgc: #b56f65,
    button-border-radius: 64px,
    text-on-dark: #fff,
    text-color: #253757,
    text-subcolor: #6c788b,
    text-color-superlight: #c0cdd7,
    transition-time: 0.5s,
  ),
  special: (
    page-bgc: #d6ca6f,
    container-horizontal-padding: 41px,
    header-bgc: #02a88a,
    header-border-radius: 0 0 10% 35%,
    header-hight-mobile: 68px,
    header-hight-desktop: 120px,
    header-margin-bottom: 30px,
    footer-border-radius: 60px 15px,
    /* 30px @ 320px increasing to 120px @ 1024px */ slider-border-radius:
    clamp(30px, calc(1.88rem + ((1vw - 3.2px) * 12.78)), 120px)
    clamp(30px, calc(1.88rem + ((1vw - 3.2px) * 12.78)), 120px) 0
    clamp(30px, calc(1.88rem + ((1vw - 3.2px) * 12.78)), 120px),
    slider-border-radius-desktop: 0 0 10% 35%,
    experiance-card-image-border-radius: 40px,
    section-bgc: rgb(175, 124, 235),
    section-border-radius: 45px 20px 45px 20px,
    about-bg-image: url(../images/Shapes-light.png),
    testimonial-color1: #8a8216,
    testimonial-color2: #4db4d6,
    testimonial-color3: #d6cb37,
    testimonial-color4: #8a1d3a,
    button-bgc: #d65a9a,
    button-hover-bgc: #bd4281,
    button-active-bgc: #8a4166,
    button-border-radius: 64px 25px 10px 25px,
    text-on-dark: #d1d091,
    text-color: #000270,
    text-subcolor: #dbd4db,
    text-color-superlight: #edcceb,
    transition-time: 1s,
  ),
);

// this will make all color-schemes

@each $theme, $theme-set in $themes {
  [color-scheme='#{$theme}'] {
    @each $name, $color in $theme-set {
      --color-#{$name}: #{$color};
    }
  }
}

// this is needed to seting up prefers-color-scheme

@mixin getTheme($color) {
  @each $theme, $theme-set in $themes {
    @if $theme == $color {
      html {
        @each $name, $color in $theme-set {
          --color-#{$name}: #{$color};
        }
      }
    }
  }
}

@media (prefers-color-scheme: light) {
  @include getTheme(blue);
}

@media (prefers-color-scheme: dark) {
  @include getTheme(black);
}

// use this to declare variable in your scss

@function getColor($color-name) {
  @return var(--color-#{$color-name});
}
